import React, {Component} from 'react';
import {connect} from 'react-redux';
import {
    setSelectedBillingAccount,
    getActiveContracts,
    setBillingAccountContractsVisibility
} from '../../actions/app';
import {
    getSelectedBillingAccountMobile,
    getSelectedBillingAccountFix,
    getSelectedAccountContracts,
    areBillingAccountContractsVisible
} from '../../selectors';

class MulticartBillingAccountSelectorComponent extends Component {

    constructor(props) {
        super(props);
    }

    showActiveContracts(evt) {
        let accountCode = evt.target.value;
        let selected = this.getSelectedAccount();
        if (accountCode && selected.type === 'MOBILE' && selected.accountCode === accountCode && !this.props.accountContractsVisible) {
            this.props.setBillingAccountContractsVisibility(true);
        }
    }

    renderBillingAccountSelectorOptions() {
        let options = this.props.billingAccounts.map((billingAccount, i) =>
            <option key={i} data-option={billingAccount.accountCode} value={billingAccount.accountCode} className="g-white1-bg">
                {billingAccount.accountCode}
            </option>
        );
        options.unshift(<option key={-1} value={null} className="g-white1-bg"></option>);
        return options;
    }

    setSelectedBillingAccount(evt) {
        let accountCode = evt.target.value;
        if (accountCode) {
            let billingAccount = this.getBillingAccountByCode(accountCode);
            this.props.setSelectedBillingAccount(billingAccount);
        }
    }
    
    getBillingAccountByCode(code){
        return this.props.billingAccounts.find(billingAccount => billingAccount.accountCode === code);
    }

    getSelectedAccount() {
        if (this.props.accountType === "MOBILE") {
            return this.props.selectedMobile;
        } else {
            return this.props.selectedFix;
        }
    }

    render() {
        let selected = this.getSelectedAccount();
        return (
            <div className="l-col l-col-small-12 l-col-medium-2 l-col-2  u-small-spacing">
                <div data-js-module="core/modules/floating-label" className="o-floating-label o-select">
                    <select id="select1" name="select" aria-describedby="floating-label__placeholder1"
                            className={'opl-select-primary opl-input--size-m' + (selected.accountCode ? ' g-greenf-bg is-not-empty' : '')}
                            value={selected.accountCode}
                            onChange={this.setSelectedBillingAccount.bind(this)}
                            onClick={this.showActiveContracts.bind(this)}>
                        {this.renderBillingAccountSelectorOptions.bind(this)()}
                    </select>
                    <span className="o-select__arrow"></span>
                    <p id="floating-label__placeholder1"
                       className="label o-floating-label__placeholder g-gray6-c ">{this.props.descriptions[this.props.accountType.toLowerCase() + 'Label']}</p>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    selectedMobile: getSelectedBillingAccountMobile(state),
    selectedFix: getSelectedBillingAccountFix(state),
    accountContractsVisible: areBillingAccountContractsVisible(state),
    accountContracts: getSelectedAccountContracts(state)
});

export default connect(mapStateToProps, {setSelectedBillingAccount, getActiveContracts, setBillingAccountContractsVisibility})(MulticartBillingAccountSelectorComponent);
